.cards {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
    width: 1300px;
    flex-wrap: wrap;
    margin-top: 70px;
}

.card_title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -70%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.card_title>p {
    text-shadow: -2px 2px 0 #111;
}

.skillper {
    width: 150px;
    height: 30px;
    border: 1px solid #f9f9f9;
    background-color: transparent;
    overflow: hidden;
    position: relative;
    color: #2b2b2b;
}

.skillper>span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #2b2b2b;
    font-size: 20px;
}

.skillper>div {
    height: 100%;
    background-color: #f9f9f9;
    width: 0;
    transition: width 1s ease-in-out;
}