#wrapHD {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 23px;
  gap: 200px;
  color: #39ff14;
  backdrop-filter: blur(5px);
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity 0.3s ease, transform 0.3s ease;
  z-index: 1000;
}

#wrapHD.visible {
  opacity: 1;
  transform: translateY(0);
}

#wrapHD>span {
  cursor: pointer;
  padding: 5px 10px;
  transition: all 0.2s ease;
  display: block;
}

#wrapHD>span:nth-child(1) {
  width: 96px;
}

#wrapHD>span:nth-child(2) {
  width: 120px;
}

#wrapHD>span:nth-child(3) {
  width: 144px;
}

#wrapHD>span:nth-child(4) {
  width: 156px;
}

#wrapHD>span:nth-child(5) {
  width: 120px;
}

#wrapHD>span:hover {
  animation-name: glitch;
  animation-duration: 0.2s;
  background-color: #39ff14;
  color: #2b2b2b;
}

#wrapHD span.active {
  background-color: #39ff14;
  color: #2b2b2b;
}

@keyframes glitch {
  25% {
    background-color: red;
    transform: translateX(-5px);
    letter-spacing: 10px;
  }

  35% {
    background-color: green;
    transform: translate(5px);
  }

  59% {
    opacity: 0;
  }

  60% {
    background-color: yellow;
    transform: translate(-5px);
  }

  100% {
    background-color: blue;
  }
}