#wrapS5 {
    width: 100%;
    height: 100vh;
    position: relative;
}

#wrapS5>h1 {
    font-family: 'NeoDunggeunGothicPro';
    color: #f9f9f9;
    font-size: 130px;
    font-weight: 400;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -150%);
}

.spanbox {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    top: 50%;
    left: 50%;
    color: #111;
    font-size: 22px;
    font-weight: bold;
    gap: 50px;
    transform: translateX(-50%);
}

.spanbox>span {
    padding: 5px 10px;
    background-color: #39ff14;
    border-radius: 2px;
}