#wrapS3 {
  width: 100%;
  height: 100vh;
  backdrop-filter: blur(2px);
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
}

.containerS3 {
  width: 1300px;
  height: 475px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 50px;
}

.cardboxS3 {
  display: flex;
  width: 100%;
  height: 400px;
  gap: 35px;
}

.cardS3 {
  width: 22.8%;
  height: 100%;
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  border: 1px solid #999;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  overflow: hidden;
}

.cardS3::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  transition: background-color 0.2s ease;
  z-index: 1;
}

.cardS3:hover::before {
  background-color: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(2px);
}

.cardS3:hover {
  border: 1px solid #39ff14;
}

.cardS3::after {
  content: "Click";
  font: monospace;
  text-shadow: -2px 2px 0px #111;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #39ff14;
  font-size: 1.5rem;
  font-weight: bold;
  opacity: 0;
  transition: opacity 0.2s ease;
  z-index: 2;
}

.cardS3:hover::after {
  opacity: 1;
}

.card-overlay {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
  padding: 0.5rem 0;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  width: 1300px;
  height: 650px;
  margin-top: 50px;
  background-color: #111;
  padding: 20px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.modal-imgbox,
.modal-infobox {
  width: 48%;
  height: 100%;
  display: flex;
}

.modal-imgbox {
  justify-content: space-between;
}

.modal-imgbox>img {
  width: 48%;
  height: 100%;
}

.modal-infobox {
  flex-direction: column;
  align-items: center;
  color: #f9f9f9;
  position: relative;
}

.infotitle {
  font-family: 'NeoDunggeunGothicPro';
  font-display: swap;
  font-size: 50px;
  margin-bottom: 15px;
}

.infoperiod {
  color: #39ff14;
  margin-bottom: 30px;
}

.infoshorts {
  white-space: pre-line;
  line-height: 150%;
  color: #f9f9f9;
  margin-bottom: 30px;
}

.infobtnbox {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 30px;
}

.infobtnbox>.infobtns {
  width: 75px;
  height: 100%;
  background-color: transparent;
  color: #39ff14;
  font-family: "VT323", monospace;
  font-size: 13px;
  cursor: pointer;
}

.infoinfo {
  line-height: 150%;
}

.infopagenation {
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  width: 100px;
}

.infopagenation>button {
  background-color: transparent;
  font-family: 'NeoDunggeunGothicPro';
  font-display: swap;
  font-size: 30px;
  cursor: pointer;
}

.accordion-title {
  width: 100%;
  height: 60px;
  border-bottom: 1px solid #999;
  color: #999;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.accordion-title.active {
  color: #39ff14;
  border-bottom: 1px solid #f9f9f9;
  font-weight: bold;
}

.accordion-content {
  padding: 20px 0;
  line-height: 150%;
  border-bottom: 1px solid #999;
}

.contribution-text {
  position: absolute;
  bottom: 80px;
}

body.modal-open #wrapHD {
  pointer-events: none;
  opacity: 0.5;
}

body.modal-open .cardS3 {
  display: none;
}