#wrapS4 {
  width: 100%;
  height: 100vh;
  backdrop-filter: blur(2px);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "VT323", monospace;
  position: relative;
}

.cmd-box {
  width: 700px;
  height: 200px;
  background-color: #0c0c0c;
  color: #39ff14;
  opacity: 0;
  transform: translateY(20px);
  overflow: hidden;
  border: 1px solid #2b2b2b;
  position: absolute;
}

.cmd-box:nth-child(2) {
  top: 20%;
  left: 10%;
}

.cmd-box:nth-child(3) {
  top: 40%;
  right: 100px;
  height: 175px;
}

.cmd-box:nth-child(4) {
  bottom: 12%;
  left: 17%;
  transform: translateY(20px);
  height: 175px;
}

.cmd-box.show {
  opacity: 1;
  transform: translateY(0);
}

.cmd-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #2b2b2b;
  padding: 4px 10px;
  color: #ccc;
  font-size: 0.9rem;
  border-bottom: 1px solid #111;
}

.cmd-title {
  font-weight: 500;
}

.cmd-buttons {
  display: flex;
  gap: 6px;
}

.cmd-buttons span {
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.cmd-buttons .min {
  background-color: #00dd00;
}

.cmd-buttons .max {
  background-color: #dddd00;
}

.cmd-buttons .close {
  background-color: rgb(230, 0, 0);
}

.cmd-body {
  padding: 15px;
  font-size: 1rem;
  line-height: 1.4;
}

.cmd-body p {
  margin: 0;
}

.cmd-body p:nth-child(3) {
  color: #f9f9f9;
  line-height: 160%;
  font-size: 16px;
}

@keyframes typing {
  from {
    opacity: 0;
    transform: translateX(-5px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}